.modern__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.modern__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.modern__navbar-links_logo {
    margin-right: 2rem;
}

.modern__navbar-links_logo img {
    width: 62px;
    height: 16px;
}

.modern__navbar-link_container {
    display: flex;
    flex-direction: row;
}

.modern__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.modern__navbar-link_container p,
.modern__navbar-sign p, 
.modern__navbar-menu_container {
    color: #fff;
    font-family: var(--font-family );
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.modern__navbar-sign button,
.modern__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background-color: #FF4820;
    left: 1153px;
    top: 55px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
}

.modern__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.modern__navbar-menu svg {
    cursor: pointer;
}

.modern__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    text-align: end;
    background-color: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.3);
}

.modern__navbar-menu_container P {
    margin: 1rem 0;
}

.modern__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .modern__navbar-link_container {
        display: none;
    }

    .modern__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .modern__navbar {
        padding: 2rem 4rem;
    }

    .modern__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 550px) {
    .modern__navbar {
        padding: 2rem;
    }

    .modern__navbar-sign {
        display: none;
    }

    .modern__navbar-menu_container {
        top: 20px;
    }

    .modern__navbar-menu_container-links-sign {
        display: block;
    }
}